/* --------- Marcellus Regular------------- */
@font-face{
    font-family:"Marcellus-Regular";
    src:url("../../../assets/fonts/Marcellus/Marcellus-Regular.eot");
    src:url("../../../assets/fonts/Marcellus/Marcellus-Regular.woff2") format('woff2'),
    url("../../../assets/fonts/Marcellus/Marcellus-Regular.woff") format('woff'),
    url("../../../assets/fonts/Marcellus/Marcellus-Regular.ttf") format('truetype'),
    url("../../../assets/fonts/Marcellus/Marcellus-Regular.svg#Marcellus-Regular") format('svg');
    font-weight:400;
    font-style: normal;
}

/* --------- Aileron Regular------------- */
@font-face{
    font-family:"Aileron-Regular";
    src:url("../../../assets/fonts/Aileron/Aileron-Regular.eot");
    src:url("../../../assets/fonts/Aileron/Aileron-Regular.woff2") format('woff2'),
    url("../../../assets/fonts/Aileron/Aileron-Regular.woff") format('woff'),
    url("../../../assets/fonts/Aileron/Aileron-Regular.ttf") format('truetype'),
    url("../../../assets/fonts/Aileron/Aileron-Regular.svg#Aileron-Regular") format('svg');
    font-weight:400;
    font-style: normal;
}

$ff-primary:"Marcellus-Regular",sans-serif;
$ff-secondary:"Aileron-Regular",sans-serif;
